/*jslint browser: true */
/*global angular */

(function (window) {
    'use strict';

    function defineModule(moment) {
        var modules = [
            'sky.autofocus',
            'sky.charts',
            'sky.check',
            'sky.checklist',
            'sky.data',
            'sky.datefield',
            'sky.daterangepicker',
            'sky.filters',
            'sky.format',
            'sky.grids',
            'sky.helpbutton',
            'sky.helpwidget',
            'sky.highlight',
            'sky.mediabreakpoints',
            'sky.modal',
            'sky.money',
            'sky.omnibar',
            'sky.pagination',
            'sky.popover',
            'sky.resources',
            'sky.scrollintoview',
            'sky.tabs',
            'sky.templating',
            'sky.textexpand',
            'sky.tiles',
            'sky.tooltip',
            'sky.validation',
            'sky.viewkeeper',
            'sky.wait',
            'sky.window',
            'sky.uiselecthelper'
        ];

        try {
            angular.module("toastr");
            modules.push('sky.toast');
        } catch (ignore) {
            /* The toastr module isn't defined.  Do not load sky.toast module */
        }

        angular.module('sky.moment', [])
            .constant('bbMoment', moment);

        angular.module('sky', modules);
    }

    if (typeof window.define === 'function' && window.define.amd) {
        window.define(['moment'], defineModule);
    } else if (window.module !== undefined && window.module && window.module.exports) {
        defineModule(window.require('moment'));
    } else {
        defineModule(window.moment);
    }
}(this));